.sections__buttons{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 20px;
    gap: 24px;

    /*position: absolute;*/
    width: 285px;
    height: 659px;
    /*left: 40px;*/
    /*top: 118px;*/

    background: #FFFFFF;
    border-radius: 8px;
}

.sections__button {
    text-align: left;
    font-family: 'Open Sans', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    border: none;
    color: #000000;
    background-color: #FFFFFF;
    transition: 0.05s;
}

.sections__button_status_active {
    font-weight: 700;
}

.sections__button:hover {
    cursor: pointer;
    color: #808080
}

.section__content {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    gap: 20px;
}